import { Component, OnInit, Injector, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import * as _ from 'underscore';
import * as moment from 'moment';
import { CookieService } from 'ngx-cookie-service';
import { MeetingService } from 'src/app/api/controllers/Meeting';
import { ModalService } from 'src/app/shared/services/modal.service';
import { MatDialog } from '@angular/material/dialog';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

@Component({
  selector: 'app-meetings-list',
  templateUrl: './meetings-list.component.html',
  styleUrls: ['./meetings-list.component.scss']
})
export class MeetingsListComponent implements OnInit {
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  gridSourceData:any;
  columnData:any;
  lstMeetingData:any;

  isMeetingDataLoaded:boolean = false;
  totalMeetingItems:any;
  meetingFieldType:string = "meetingId";
  meetingOrder:boolean = false;
  selectedInput:string = "";
  columnField: any = {}
  isMobile:boolean = false;


  ItemMeetingStartIndex:any = 0;
  ItemMeetingEndIndex:any;
  itemMeetingLimit:any = 15;
  modalService:any;


  constructor(private router: Router,
    private meetingService:MeetingService,
    private injector: Injector,
    private cookieService:CookieService,
    public dialog: MatDialog) { 
      this.modalService = this.injector.get(ModalService)
    }

  ngOnInit(): void {
    this.getMeetingList();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.columnData = [{
        text: 'Date',
        datafield: 'meetingDate',
        width: 100,
        cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
        },
        renderer: columnrenderer
    },{
        text: 'From Time',
        datafield: 'fromTime',
        minwidth: 100,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer,
    },{
        text: 'To Time',
        datafield: 'toTime',
        cellsrenderer: cellsrenderer,
        minwidth: 100,
        renderer: columnrenderer
    },{
        text: 'Type',
        datafield: 'meetingTypeId',
        cellsrenderer: cellsrenderer,
        minwidth: 100,
        renderer: columnrenderer
    },{
        text: 'Catgeory',
        datafield: 'meetingCategoryId',
        cellsrenderer: cellsrenderer,
        minwidth: 100,
        renderer: columnrenderer
    },{
        text: 'Subject',
        datafield: 'subject',
        cellsrenderer: cellsrenderer,
        minwidth: 150,
        renderer: columnrenderer
    },{
        text: 'Recepients',
        datafield: 'meetingRecipientsId',
        cellsrenderer: cellsrenderer,
        minwidth: 80,
        renderer: columnrenderer
    },{
      text: 'Status',
      datafield: 'meetingStatusId',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },
    // {
    //     text: 'Actions',
    //     cellsalign: 'center', 
    //     align: 'center',
    //     width: 120,
    //     cellsrenderer: (row: number): string => {
    //       return '<div class="simple-actions">'
    //       +'<a href="javascript:void(0)" class="mr-3" onClick="viewPermitEvent('+ row +')"><i class="fa fa-eye icon view" aria-hidden="true"></i></a>'
    //       +'<a href="javascript:void(0)" class="mr-3" onClick="editPermitEvent('+ row +')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a>'
    //       +'<a href="javascript:void(0)" onClick="statusPermitEvent('+ row +')"><i class="fa fa-check-circle icon delete" aria-hidden="true"></i></a></div>'
    //     },
    //     renderer: columnrenderer
    // }
  ];
  }

  getMeetingList() {
    if(window.innerWidth <= 991)
      this.isMobile = true;
    else
      this.isMobile = false;
    let params = {
     apartmentId : parseInt(this.cookieService.get('apartmentId'))
    }
    this.meetingService.getMeetingByApartmentId(params).subscribe((res : any) => {
      this.lstMeetingData = res;
      console.log(res);
      this.gridSourceData = {
        localdata: this.lstMeetingData,
        datatype: "array"
      }
      this.lstMeetingData = new jqx.dataAdapter(this.gridSourceData);
      // this.totalMeetingItems = this.lstMeetingData.length;
      // if(this.totalMeetingItems>this.itemMeetingLimit){
      //   this.ItemMeetingEndIndex = this.itemMeetingLimit;
      // }
      // else {
      //   this.ItemMeetingEndIndex = this.totalMeetingItems;
      // }
      this.isMeetingDataLoaded = true;
    },
    error => {
      console.log(error);
    });
  }

  addMeeting() {
    this.router.navigate(['ams/meetings/create']);
  }
  
  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  addMeetingMinutes(data){
    this.modalService.showMeetingAddMinutesModal(data);
  }
  viewMeetingInfo(data){
    data.isEdit = false;
    this.modalService.showMeetingEditorViewModal(data);
  }
  editMeetingInfo(data){
    data.isEdit = true;
    this.modalService.showMeetingEditorViewModal(data);
  }
}
